import React, {Component} from 'react';
import './App.css';
import {Search} from './components/Search';
import Header from './components/Header';
import {Add} from './components/Add';
import {AnnounsmentList} from './components/AnnounsmentList';
import {Edit} from './components/Edit';

export default class App extends Component {

state = {
    announcements : [{
        id:'0000',
        title: '1',
        description: 'lorem10',
        date: '22.10.1985'
    },
        {id:'0001',
            title: '11',
            description: 'lorem101111',
            date: '22.09.1985'
        }],
    tempEditing: {
        id:'1',
        title:'11',
        description:'11',
        date:'2011-11-11'
    }
}
delItem = (id) => {
    this.setState(
        ({announcements}) => {
        
            return{
                announcements: announcements.filter(a => a.id!==id),
            };

        }
    );
}
    addItem = (title, description) => {
        let date = new Date();
        let data = date.getDate()+'.0'+
            date.getMonth()+'.'+date.getFullYear()+'  '+date.getHours()+':'+date.getMinutes()+':'+date.getSeconds();
    const newAnnounsment = {
        id: Date.now(),
        title: title,
        description: description,
        date: data}
        this.setState(
            ({announcements}) => {

                return{
                    announcements: [...announcements, newAnnounsment],
                };

            }
        );
    }

    onEdit= (id, title, description, date) => {
        const newData ={id: id, title: title, description: description, date: date};
        this.setState(
            ({tempEditing}) => {
                return{
                    tempEditing: {...tempEditing,id: id, title: title, description: description, date:date}


            };
            });
        console.log(this.state.tempEditing);
    }
    setEdited= (id, title, description, date) => {
        this.setState(
            ({announcements}) => {

                return{
                    announcements: announcements.filter(a => a.id!==id),
                };

            }
        );
    }

    filterItems = (qwery) => {
        this.setState(
            ({announcements}) => {

                return{
                    announcements: announcements.filter(a => a.title.includes(qwery)),
                };

            }
        );
    }
    render(){

        return (
            <>
                <div className="Header">
                    <Header />
                </div>
                <div className="Search">
                    <Search filterItems={this.filterItems}/>
                </div>
            <div className="App">
               <Add AddAnnounc={this.addItem}/>
               <AnnounsmentList
                   announcements={this.state.announcements}
                   onDeleted={this.delItem}
                onEdit={this.onEdit}/>
                    <Edit data={this.state.tempEditing} setEdited={this.setEdited}/>

            </div>
            </>
        );
    }}
